module.exports = "# QuerySchemeMixin\r\n\r\n## 多方案查询\r\n\r\n属性 | 默认值 | 说明\r\n:---------------|:-------------|:-------------\r\ntitle | | 默认查询方案名称\r\nexcludeInputNames | [\"begindate\",\"enddate\"] | 保存查询方案时，排除的输入项 name\r\n\r\n### 输入项的 `name` 属性与绑定的值应同名。如以下 keywords、shownotused\r\n```html\r\n<div class=\"itemfield-row\">\r\n    <input name=\"keywords\" class=\"\" type=\"search\" data-dojo-props=\"label:'筛选条件',value:at('rel:','keywords')\" data-dojo-type=\"dijit/form/TextBox\" data-dojo-mixins=\"desktop/widgets/ItemField\" placeholder=\"商品名称/货号/条码/规格/型号\">\r\n    <button type=\"submit\" data-dojo-props=\"label:'查询',title:'快捷查询：F2',onClick:lang.hitch(this,'onQueryClick'),class:'btn-query '\" data-dojo-type=\"dijit/form/Button\" data-dojo-mixins=\"desktop/widgets/VisibleMixin\"></button>\r\n    <div name=\"shownotused\" data-dojo-type=\"widget/FormField\" data-dojo-props=\"label:'显示无业务数据商品'\">\r\n                            <input name=\"shownotused\" type=\"checkbox\" value=\"\" data-dojo-type=\"dijit/form/CheckBox\" data-dojo-props=\"checked:at('rel:','shownotused'),onClick:lang.hitch(this,'toggleTreeState')\">\r\n                        </div>\r\n</div>\r\n```\r\n\r\n### 方案列表组件 `v-query-scheme`\r\n\r\n当`v-query-scheme`显示时，`v-query-scheme` 之前的元素会显示在右侧；`v-query-scheme` 之后的元素会显示在下方；class 包含 `query-scheme-hidden` 的元素隐藏\r\n\r\n```html\r\n<!--显示在右侧-->\r\n<v-query-scheme v-bind=\"$data\" v-if=\"visible\" @click-scheme=\"onClick\" @dblclick-scheme=\"onDblClick\" @change-scheme=\"onChange\" @add-scheme=\"onAdd\" @remove-scheme=\"onRemove\" @close=\"hide\" data-dojo-attach-point=\"queryscheme\"></v-query-scheme>\r\n<!--显示在下边-->\r\n```\r\n\r\n![avatar](https://dengrc.github.io/lkdocs/image/pc/QQ截图20191224162010.jpg)\r\n\r\n![avatar](https://dengrc.github.io/lkdocs/image/pc/QQ截图20191224164808.jpg)\r\n\r\n### 打开多方案查询按钮\r\n\r\n```html\r\n<span class=\"v-query-scheme-switch flat-chevron-right query-scheme-hidden\" data-dojo-attach-event=\"onclick:onOpenQueryScheme\"></span>\r\n```\r\n\r\n### 多方案下拉选择\r\n\r\n```html\r\n<select class=\"v-query-scheme-select query-scheme-hidden\" data-dojo-type=\"dijit/form/Select\" data-dojo-props=\"store:at(this,'querySchemeStore'),labelAttr:'label',value:at(this,'currentQueryScheme'),_focusChangeActive:true,onChange:lang.hitch(this,'onChangeQueryScheme')\"></select>\r\n```\r\n\r\n### 保存方案按钮\r\n\r\n```html\r\n<button data-dojo-props=\"label:'保存并查询',onClick:lang.hitch(this,'onSaveQuerySchemeClick'),class:'btn-ok ',visible:at(this,'showQuerySchemeButton')\" data-dojo-type=\"dijit/form/Button\" data-dojo-mixins=\"desktop/widgets/VisibleMixin\"></button>\r\n```\r\n\r\n## 收缩查询条件（看以下注释）\r\n\r\n```html\r\n<!--open 收缩状态，lineClamp 收缩的显示行数-->\r\n<div class=\"container\" data-dojo-attach-point=\"showMore\" data-dojo-type=\"desktop/widgets/ShowMore\" data-dojo-props=\"open:false,lineClamp:2,resizeView:this\">\r\n    <div class=\"itemfield-row\">\r\n      ......\r\n      <!--class 包含 default-hidden 的元素，在收缩状态会隐藏-->\r\n      <div name=\"shownotused\" class=\"default-hidden\" data-dojo-type=\"widget/FormField\" data-dojo-props=\"label:'显示无业务数据商品'\">\r\n            <input name=\"shownotused\" type=\"checkbox\" value=\"\" data-dojo-type=\"dijit/form/CheckBox\" data-dojo-props=\"checked:at('rel:','shownotused'),onClick:lang.hitch(this,'toggleTreeState')\">\r\n        </div>\r\n      <!--展开按钮-->\r\n      <button class=\"button-showmore\" data-dojo-props=\"label:'更多条件'\" data-dojo-type=\"desktop/widgets/LinkButton\" data-open-label=\"更多条件\" data-close-label=\"更多条件\"></button>\r\n    </div>\r\n</div>\r\n```\r\n\r\n## 日期范围选择\r\n\r\n### 快捷选择\r\n```html\r\n<div class=\"button-group-line\" data-dojo-attach-point=\"dateRange\" data-dojo-type=\"widget/ButtonGroup\" data-dojo-props=\"value:g.at(this,'dateRangeValue'),clamp:4,onChange:lang.hitch(this,'_dateClick'),items:this.dateRangeItems,date:'1'\"></div>\r\n```\r\n\r\n### 日期范围 \r\n`name`,`end-name` 与 `data-dojo-props`: `start`,`end` 字段一致\r\n```html\r\n<input name=\"beginmodifydate\" end-name=\"endmodifydate\" required=\"\" type=\"text\" data-dojo-type=\"desktop/widgets/DateRangeBox\" data-dojo-attach-point=\"formDateRange\" data-dojo-props=\"label:'日期范围',showLabel:false,start:at('rel:','beginmodifydate'),end:at('rel:','endmodifydate'),onChange:lang.hitch(this,'onBeginDateChange')\" data-dojo-mixins=\"desktop/widgets/ItemField\">\r\n```\r\n```html\r\n<input name=\"begindate\" end-name=\"enddate\" required=\"\" type=\"text\" data-dojo-type=\"desktop/widgets/DateRangeBox\" data-dojo-attach-point=\"formDateRange\" data-dojo-props=\"label:'日期范围',showLabel:false,start:at('rel:','begindate'),end:at('rel:','enddate'),onChange:lang.hitch(this,'onBeginDateChange')\" data-dojo-mixins=\"desktop/widgets/ItemField\">\r\n```"

